import React from "react"
import Header from "../shared/components/header.jsx"
import Tabs from "../shared/components/tabs.jsx"
import styled from "styled-components"
import Body from "../shared/components/body.jsx"

import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/swiper.scss'

const Inbox = () => {

	const InsightsArray = [
		{
			date: 1504936573,
			status: "read",
			title: "this is a teaser title",
			text: "this is the best teaser text ever",
			image: "teaser-image",
			insightId: "f1c70a2e-9ed9-418b-8d44-d71c38c374ce"
		},
		{
			date: 1504836573,
			status: "unread",
			title: "this is a teaser title with some very very very very very very very very very very very very very very very very very very very very very very very very very very very very very very very very very very very very very very very very very very very long text",
			text: "this is the second best teaser text ever",
			image: "teaser-image2",
			insightId: "18b1c762-6c42-4a5c-a332-d762f9057df5"
		},
		{
			date: 1501836573,
			status: "unread",
			title: "transactions",
			text: "there are some transactions in this story",
			image: "teaser-image3",
			insightId: "447e3a4b-ee1d-458e-9c9f-d040abed82b0"
		}
	];
	return (
	<Container>
		<Header>
			INBOX
		</Header>
		<Body>
			<Tabs active="1" />
			<Swiper renderArrowNext={false} renderArrowPrev={false} swipeable={true} >
				{InsightsArray.map(e => {
					return <SwiperSlide key={e.insightId}
								spaceBetween={50}
								slidesPerView={1}
								onSlideChange={() => console.log('slide change')}
								onSwiper={(swiper) => console.log(swiper)}>
							<StyledCard>
							<span>{e.date}</span>
							<h3>{e.title}</h3>
							<span>{e.text}</span>
							<img src={"./images/"+e.image+".png"}></img>
						</StyledCard>
					</SwiperSlide>
				})}
			</Swiper>
		</Body>
	</Container>)
}
	
export default Inbox

const Container = styled.div`
	width: 100%;
`

const StyledCard = styled.div`
	width: 200px;
	background-color: yellow;
`